.header {
  display: flex;
  justify-content: space-between;
  margin: 45px auto 40px;
  width: 69%;
  max-width: 880px;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
}

@media screen and (max-width: 950px) {
  .header {
    width: 90%;
  }
}

@media screen and (max-width: 750px) {
  .header {
    width: 100%;
    margin-top: 28px;
  }
}
